
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section,summary{display:block
}
audio,canvas,progress,video{display:inline-block;vertical-align:baseline
}
audio:not([controls]){display:none;height:0
}
[hidden],template{display:none
}
a{background-color:transparent
}
a:active,a:hover{outline:0
}
abbr[title]{border-bottom:1px dotted
}
b,strong{font-weight:bold
}
dfn{font-style:italic
}
h1{font-size:0.32rem
}
mark{background:#ff0;color:#000
}
small{font-size:80%
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-0.08rem
}
sub{bottom:-0.04rem
}
img{border:0
}
svg:not(:root){overflow:hidden
}
hr{box-sizing:content-box;height:0
}
pre{overflow:auto
}
code,kbd,pre,samp{font-family:monospace,monospace;font-size:0.16rem
}
button,input,optgroup,select,textarea{color:inherit;font:inherit;margin:0
}
button{overflow:visible
}
button,select{text-transform:none
}
button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:button;cursor:pointer
}
button[disabled],html input[disabled]{cursor:default
}
button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0
}
input{line-height:normal
}
input[type="checkbox"],input[type="radio"]{box-sizing:border-box;padding:0
}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button{height:auto
}
input[type="search"]{-webkit-appearance:textfield;box-sizing:content-box
}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
fieldset{border:1px solid #c0c0c0;
}
legend{border:0;padding:0
}
textarea{overflow:auto
}
optgroup{font-weight:bold
}
table{border-collapse:collapse;border-spacing:0
}
td,th{padding:0
}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{padding:0;margin:0;-webkit-tap-highlight-color:rgba(0,0,0,0);box-sizing:border-box
}
ol,ul{list-style:none
}
a{text-decoration:none
}
a,button,input{outline:0;
}
audio,canvas,img,video{vertical-align:middle
}
.clearfix:before,.clearfix:after{content:" ";display:table
}
.clearfix:after{clear:both
}
body {
  background-color: #010017;
}
.container {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Microsoft YaHei', sans-serif;
  position: relative;
  min-width: 320px;
  max-width: 750px;
  margin: 0 auto;
  font-size: 0.32rem;
}
[v-cloak] {
  display: none;
}
.demo {
  text-align: center;
}
.demo .btn {
  width: 7.46666667rem;
}
.radio-wrapper .list {
  padding-left: 0.24rem;
  padding-right: 0.24rem;
  padding-top: 0.46666667rem;
}
.radio-wrapper .list li {
  background-color: #fff;
  width: 4.61333333rem;
  height: 6.50666667rem;
  position: relative;
  margin-bottom: 0.26666667rem;
  float: left;
  display: table;
}
.radio-wrapper .list li:nth-child(even) {
  float: right;
}
.radio-wrapper .list li .live {
  position: absolute;
  background-color: #2aa2fe;
  width: 2rem;
  height: 0.66666667rem;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
  left: -0.14666667rem;
  top: 0.38666667rem;
  z-index: 99;
  display: table;
}
.radio-wrapper .list li .live p {
  color: #fff;
  font-size: 0.32rem;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}
.radio-wrapper .list li .liveoff {
  position: absolute;
  background-color: #ff0000;
  width: 2rem;
  height: 0.66666667rem;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
  left: -0.14666667rem;
  top: 0.38666667rem;
  z-index: 99;
  display: table;
}
.radio-wrapper .list li .liveoff p {
  color: #fff;
  font-size: 0.32rem;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}
.radio-wrapper .list li .link {
  display: block;
  width: 4.32rem;
  height: 4.32rem;
  position: absolute;
  left: 0.14666667rem;
  right: 0.14666667rem;
  top: 0.13333333rem;
}
.radio-wrapper .list li .user {
  width: 4.32rem;
  display: block;
}
.radio-wrapper .list li .play {
  width: 0.8rem;
  position: absolute;
  left: 0.4rem;
  top: 3.33333333rem;
}
.radio-wrapper .list li .add {
  position: absolute;
  font-size: 0.4rem;
  font-weight: bold;
  color: #f919b6;
  z-index: 99;
  right: 0.4rem;
  top: 4.13333333rem;
  -webkit-animation: fadeOutUp 2s .2s ease both;
}
.radio-wrapper .list li .user-wrapper {
  position: absolute;
  left: 0.14666667rem;
  top: 4.66666667rem;
  width: 4.26666667rem;
}
.radio-wrapper .list li .user-wrapper .name {
	font-weight:700;
  color: #333;
  font-size: 0.34666667rem;
  display: inline-block;
  width: 2rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.radio-wrapper .list li .user-wrapper .num {
  color: #f919b6;
  font-size: 0.34666667rem;
  display: inline-block;
  float: right;
}
.radio-wrapper .list li .do-btn {
  background-color: #f919b6;
  text-align: center;
  border-radius: 0.2rem;
  width: 4.08rem;
  height: 0.93333333rem;
  bottom: 0.26666667rem;
  left: 0.26666667rem;
  position: absolute;
  display: table;
}
.radio-wrapper .list li .do-btn p {
  display: table-cell;
  vertical-align: middle;
  font-size: 0.4rem;
  color: #fff;
}
.radio-wrapper .list li .had-btn {
  background-color: #ffb9e8;
  text-align: center;
  border-radius: 0.2rem;
  width: 4.08rem;
  height: 0.93333333rem;
  bottom: 0.26666667rem;
  left: 0.26666667rem;
  position: absolute;
  display: table;
}
.radio-wrapper .list li .had-btn p {
  display: table-cell;
  vertical-align: middle;
  font-size: 0.4rem;
  color: rgba(255, 255, 255, 0.6);
}


